        this.bannerList = []
      }
    },

    // Âä†ËΩΩÁ≥ªÁªüÈÖçÁΩÆ
    async loadConfigs() {
      try {
        console.log('=== ÂºÄÂßãÂä†ËΩΩÁ≥ªÁªüÈÖçÁΩÆ ===')

        // Ëé∑ÂèñÊâÄÊúâÈÖçÁΩÆÔºà‰∏ç‰º†keysÂèÇÊï∞Ôºâ
        const res = await getConfigs()

        console.log('Á≥ªÁªüÈÖçÁΩÆAPIÂìçÂ∫îÂÆåÊï¥Êï∞ÊçÆ:', JSON.stringify(res))
        console.log('ÂìçÂ∫îÁ†Å:', res.code)
        console.log('ÂìçÂ∫îÊï∞ÊçÆ:', res.data)
        console.log('Êï∞ÊçÆÁ±ªÂûã:', typeof res.data)

        if (res.code === 200 && res.data) {
          const configs = []

          // Â∞ÜÈÖçÁΩÆÂØπË±°ËΩ¨Êç¢‰∏∫Êï∞ÁªÑÊ†ºÂºè
          if (typeof res.data === 'object' && !Array.isArray(res.data)) {
            const keys = Object.keys(res.data)
            console.log('ÈÖçÁΩÆÈîÆÂàóË°®:', keys)

            keys.forEach(key => {
              const value = res.data[key]
              console.log(`Â§ÑÁêÜÈÖçÁΩÆÈ°π: ${key} = ${value}`)

              configs.push({
                paramKey: key,
                paramValue: String(value),  // Á°Æ‰øùÂÄºÊòØÂ≠óÁ¨¶‰∏≤
                displayValue: this.formatConfigValue(key, value),
                remark: this.getConfigRemark(key)
              })
            })
          } else if (Array.isArray(res.data)) {
            // Â¶ÇÊûúËøîÂõûÁöÑÊòØÊï∞ÁªÑÔºåÂèØËÉΩÊòØÂêéÁ´ØËøîÂõû‰∫ÜConfigVOÂàóË°®
            console.log('ÂêéÁ´ØËøîÂõûÊï∞ÁªÑÊ†ºÂºèÔºåËΩ¨Êç¢Â§ÑÁêÜ')
            this.configList = res.data.map(item => ({
              paramKey: item.paramKey,
              paramValue: item.paramValue,
              displayValue: this.formatConfigValue(item.paramKey, item.paramValue),
              remark: item.remark || item.paramKey
