          console.log('ÊúÄÁªàÈÖçÁΩÆÂàóË°®Êï∞Èáè:', configs.length)
          console.log('ÊúÄÁªàÈÖçÁΩÆÂàóË°®:', JSON.stringify(configs))

          this.configList = configs

          console.log(`‚úÖ ÈÖçÁΩÆÂä†ËΩΩÊàêÂäüÔºåÂÖ±${configs.length}‰∏™ÈÖçÁΩÆÈ°π`)

