        } else {
          console.log('ÂìçÂ∫îÁ†Å‰∏çÊòØ200ÊàñÊ≤°ÊúâÊï∞ÊçÆ')
          uni.showToast({
            title: 'ÈÖçÁΩÆ‰∏∫Á©∫',
            icon: 'none',
            duration: 2000
          })
        }
      } catch (error) {
        console.error('=== Âä†ËΩΩÁ≥ªÁªüÈÖçÁΩÆÂ§±Ë¥• ===')
        console.error('ÈîôËØØ‰ø°ÊÅØ:', error)
        console.error('ÈîôËØØËØ¶ÊÉÖ:', JSON.stringify(error))

        uni.showToast({
          title: 'Âä†ËΩΩÈÖçÁΩÆÂ§±Ë¥•',
          icon: 'none'
        })

        // Â§±Ë¥•Êó∂ÂàõÂª∫Á©∫ÈÖçÁΩÆÂàóË°®
        this.configList = []
      }
    },

    // ÊòæÁ§∫ÈÖçÁΩÆÂºπÁ™ó
