        'per_km_price': `¬•${value}ÂÖÉ/ÂÖ¨Èáå`,
        'weather_rate': `${value}ÂÄç`,
        'platform_rate': `${value}%`,
        'min_delivery_fee': `¬•${value}ÂÖÉ`,
        'max_delivery_distance': `${value}ÂÖ¨Èáå`,
        'service_time': `${value}Â∞èÊó∂`
