      return formatMap[key] || value
    },

    // Ëé∑ÂèñÈÖçÁΩÆÁöÑÂ§áÊ≥®ÂêçÁß∞
    getConfigRemark(key) {
      const remarkMap = {
        'base_price': 'Ëµ∑Ê≠•‰ª∑',
        'per_km_price': 'ÂÖ¨ÈáåÂçï‰ª∑',
        'weather_rate': 'Â§©Ê∞îÁ≥ªÊï∞',
        'platform_rate': 'Âπ≥Âè∞Ë¥πÁéá',
        'min_delivery_fee': 'ÊúÄ‰ΩéÈÖçÈÄÅË¥π',
        'max_delivery_distance': 'ÊúÄÂ§ßÈÖçÈÄÅË∑ùÁ¶ª',
        'service_time': 'ÊúçÂä°Êó∂Èó¥'
      }

      return remarkMap[key] || key
    },

    // ‰∏ãÊãâÂà∑Êñ∞
    async onRefresh() {
      console.log('=== ÂºÄÂßãÂà∑Êñ∞È¶ñÈ°µÊï∞ÊçÆ ===')

      this.refreshing = true

      try {
        // ÂêåÊó∂Âà∑Êñ∞ËΩÆÊí≠Âõæ„ÄÅÈÖçÁΩÆÂíåÂúàÂ≠êÂä®ÊÄÅ
        await Promise.all([
          this.loadBanners(),
          this.loadConfigs(),
          this.loadForumPosts()
        ])

        uni.showToast({
          title: 'Âà∑Êñ∞ÊàêÂäü',
          icon: 'success'
        })
      } catch (error) {
        console.error('Âà∑Êñ∞Â§±Ë¥•:', error)
        uni.showToast({
          title: 'Âà∑Êñ∞Â§±Ë¥•',
          icon: 'none'
        })
      } finally {
        // Âª∂ËøüÈáçÁΩÆÂà∑Êñ∞Áä∂ÊÄÅÔºåÁ°Æ‰øùÂä®ÁîªÂÆåÊàê
        setTimeout(() => {
          this.refreshing = false
        }, 500)
      }
    },

    // ËΩÆÊí≠ÂõæÁÇπÂáª‰∫ã‰ª∂
    onBannerClick(banner) {
      if (banner.content) {
        // Ê†πÊçÆcontentÁ±ªÂûãÊâßË°å‰∏çÂêåÊìç‰Ωú
        if (banner.content === 'join_rider') {
          // Ë∑≥ËΩ¨Âà∞È™ëÊâãËÆ§ËØÅÈ°µÈù¢
          uni.navigateTo({
            url: '/pages/rider/auth'
          })
        } else {
          uni.showToast({
            title: banner.title,
            icon: 'none'
          })
        }
      } else {
        uni.showToast({
          title: banner.title,
          icon: 'none'
        })
      }
    },

    // Ë∑≥ËΩ¨Âà∞ÂèëÂ∏ÉËÆ¢ÂçïÈ°µÈù¢
    navigateToPublish(type) {
      // Â∞ÜÁ±ªÂûãËΩ¨Êç¢‰∏∫ÊúçÂä°Á±ªÂûãID
      const typeMap = {
        'buy': 1,    // Â∏Æ‰π∞
        'send': 2,   // Â∏ÆÈÄÅ
        'fetch': 3,  // Â∏ÆÂèñ
        'all': 4     // ÂÖ®ËÉΩ
      };

      const serviceType = typeMap[type] || 1;

      uni.navigateTo({
        url: `/pages/order/create?serviceType=${serviceType}`
      });
    },

    // Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïÂàóË°®
    goToOrders() {
      uni.switchTab({
        url: '/pages/orders/orders'
      });
    },

    // Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïËØ¶ÊÉÖ
    goToOrderDetail(orderId) {
      uni.showToast({
        title: 'ËÆ¢ÂçïËØ¶ÊÉÖÂäüËÉΩÂºÄÂèë‰∏≠...',
        icon: 'none'
      });
      // TODO: Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïËØ¶ÊÉÖÈ°µÈù¢
      // uni.navigateTo({
      //   url: `/pages/order/detail?id=${orderId}`
      // });
    },

    // Âä†ËΩΩÂúàÂ≠êÂä®ÊÄÅ
    async loadForumPosts() {
      try {
        const res = await getPostList({
          page: 1,
          size: 3,
          orderBy: 'createTime,desc'
        })
        console.log('üîç È¶ñÈ°µÂúàÂ≠êÂìçÂ∫î:', res)
        if (res.code === 200) {
           let list = []
           if (res.data) {
              if (Array.isArray(res.data)) {
                  list = res.data
              } else if (Array.isArray(res.data.records)) {
                  list = res.data.records
              }
           }
          this.forumPosts = list
          console.log('‚úÖ È¶ñÈ°µÂúàÂ≠êÂä†ËΩΩÊàêÂäü:', this.forumPosts.length)
        }
      } catch (error) {
        console.error('‚ùå Âä†ËΩΩÈ¶ñÈ°µÂúàÂ≠êÂ§±Ë¥•:', error)
      }
    },

    // Ë∑≥ËΩ¨Âà∞ÂúàÂ≠êÂàóË°®
    goToForum() {
      uni.navigateTo({
        url: '/pages/forum/index'
      })
    },

    // Ë∑≥ËΩ¨Âà∞Â∏ñÂ≠êËØ¶ÊÉÖ
    goToPostDetail(postId) {
      uni.navigateTo({
        url: `/pages/forum/detail?id=${postId}`
      })
    },

    // ÁÇπËµûÂ§ÑÁêÜ
    async onPostLike(postId) {
      try {
        const res = await likePost(postId)
        if (res.code === 200) {
          // ÈÄÇÈÖçÊñáÊ°£‰∏≠ÁöÑËøîÂõûÁªìÊûúÔºödata.liked, data.likeCount
          const index = this.forumPosts.findIndex(p => p.id === postId)
          if (index !== -1) {
            const post = this.forumPosts[index]
            post.liked = res.data.liked
            post.likeCount = res.data.likeCount
            this.forumPosts[index] = post
          }
        }
      } catch (error) {
        uni.showToast({ title: 'Êìç‰ΩúÂ§±Ë¥•', icon: 'none' })
      }
    }
  }
};
</script>

